<template>
    <transition name="slider">
    <div class="second-page">
      <div class="group">
          <div class="img-1 img "></div>
          <div class="img-2 img "></div>
          <div class="img-3 img "></div>
          <div class="img-4 img "></div>
      </div>
      <p class="text-center">
           <span class="Button enter " >
            <input type="file" accept="image/*" class="js-btn-upload" data-upload="1" @change="uploadInit" ref="upload">
          </span>
      </p>
      <loading v-show="loadingOnOff"></loading>
    </div>

    </transition>
</template>

<script>
  // import {doMain} from  'api/url';
  import axios from 'common/js/http';
  import  loading from "base/loading";
  import {mapGetters,mapMutations} from 'vuex'
    export default {
      data(){
        return{
          imgSrc:'',
          options:{
            token:'',
            imgSrc:'',
            uploadTimer:'',
            createCanvasTimer:'',
          },
        }
      },
      components: {
        loading,
      },
        name: "",
        props:{

        },
      computed:{
        ...mapGetters([
          'loadingOnOff',
        ])
      },
        methods:{
          upimgtime(){
            _czc.push(['_trackEvent', '上传图片', '时间', '', this.options.uploadTimer, 'js-btn-upload']);
          },
          upimgsize(size){
            _czc.push(['_trackEvent', '上传图片', '大小', '', size/1024, 'js-btn-upload']);
          },
          uploadInit(){
          let file = this.$refs.upload.files[0];
          let fileType =['image/jpeg','image/jpg','image/png'];
          let filename = file.name;
          let type = ('image/'+filename.substring(filename.lastIndexOf('.')+1,filename.length)).toLocaleLowerCase();
          if ((fileType.join().toString()).indexOf(type) < 0) {
            alert('只允许上传JPG、PNG格式图片！');
            this.$refs.upload.value = '';
            return false;
          }
          this.uploadCheck(file);
          this.$refs.upload.value = '';
        },
          uploadCheck(file){
            let token = this.options.token;
            let _this=this
            let startTimer = new Date().getTime();{
              if(token==''){
                this.getUploadToken(function(){
                  _this.uploadImg(file)
                });
              }else{
                _this.uploadImg(file);
              }
            }
          },
          getUploadToken(cbk){
            let _this = this,callback = cbk || function(){};
            this.$jsonp(process.env.API_HOST+'public/index/qn_token').then((res)=>{
              // console.log(res)
              this.options.token = res;
              callback();
            })
            // axios.get(process.env.API_HOST+'public/index/qn_token',{withCredentials: false}).then((res)=>{
            //   // console.log(res)
            //   this.options.token = res.data;
            //   callback();
            // });
          },
          uploadImg(file){
            let Qiniu_UploadUrl = "http://up.qiniu.com",
              ops = this.options,
              token = ops.token;
            let startTimer = new Date().getTime();
            let formData = new FormData();
            let _this=this
            formData.append('token', token.upload_token);
            formData.append('key', token.key);
            formData.append('file', file);
            let xhr = new XMLHttpRequest();
            _this.setLoadingOnOff(true)
            xhr.open('POST', Qiniu_UploadUrl, true);
            xhr.onreadystatechange = function(response) {
              // console.log(xhr.readyState)
              // console.log(xhr.responseText)
              // console.log(xhr.status)
              if(xhr.readyState == 4 && xhr.status == 200 && xhr.responseText != "") {
                let imgSrc='http://mtapplet.meitudata.com/'+token.key ;
                _this.options.imgSrc = imgSrc;
                ops.uploadTimer = new Date().getTime() - startTimer;
                _this.setLoadingOnOff(false);
                _this.imgSrc=imgSrc;
                _this.options.token='';
                _this.$emit('firstPage',[_this.imgSrc,_this.options])
                _this.upimgtime();
                _this.upimgsize(file.size);
              }else if(xhr.status != 200 && xhr.responseText) {
                _this.setLoadingOnOff(false);
                alert('上传出错了，请返回重试~');
              }
              // this.options.token='';//一个token只能用一次
            };
            xhr.send(formData);
          },
          ...mapMutations({
            setLoadingOnOff:'SET_LOADING'
          })
       },

    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
.second-page{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 20;
  width: 100%;
  height: 17.8rem;
  background: url("../common/image/bg-home-tips.jpg") no-repeat;
  background-size: 100% 100%;
}
.group{
  position: relative;
  top: 3.333333rem;
  .img{
    position: absolute;
    width: 4.76rem;
    height: 5.8666666rem;
    background: url("../common/image/sprite.png") no-repeat;
    background-size: 1300px auto;
  }
  .img-1{
      top: 0;
      left: 0.586666666rem;
      background-position: 0 0;
  }
  .img-2{
    top: 0;
    right:0.586666666rem ;
    background-position: -10.853333333rem 0;
  }
  .img-3{
    top:5.3rem;
    left: 0.586666666rem;
    background-position: -5.43rem 0;
  }
  .img-4{
    top:5.3rem;
    right:0.586666666rem ;
    background-position: 0 -6.533333rem ;
  }
}
.text-center{
  text-align: center;
}
.Button{
  position: absolute;
  width: 5.2rem;
  height: 1.2rem;
  top: 14.48rem;
  left: 50%;
  margin-left: -2.6rem;
  background-image: url("../common/image/sprite.icons.png");
  background-position: -7.333333rem -1.8rem;
  background-size: 941px auto;
  z-index: 10;
}
.Button input{
  display: block;
  width: 100%;
  height: 100%;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
}
.slider-enter-active, .slider-leave-active{
  transition: all 0.4s
}
.slider-enter, .slider-leave-to{
  transform: translate3d(100%, 0, 0)
}
</style>



