<template>
    <div class="wrap">
    <div class="index-wrap">
        <img src="../common/image/home-gif.gif" class="home-gif">
        <p class="text-center">
          <span class="Button" v-if="intoOnOff"  onclick="_czc.push(['_trackEvent', '首次使用', '点击数', '', '', 'Button']);" @click="intoSecondPage"></span>
          <span class="Button enter "  onclick="_czc.push(['_trackEvent', '再次使用', '点击数', '', '', 'Button enter']);" v-else>
            <input type="file" accept="image/*" class="js-btn-upload" data-upload="1" @change="uploadInit" ref="upload" id="upload">
          </span>
        </p>
    </div>
    <second-page v-if="pageOnOff" @firstPage="firstPage"></second-page>
    <upload-confirm v-if="confirmOnOff" @select="showConfirm" :imgSrc="imgSrc" :options="options"></upload-confirm>
    <!--<result></result>-->
    <loading v-show="loadingOnOff"></loading>
    <router-view></router-view>
    </div>
</template>

<script type="text/ecmascript-6">
  import  secondPage from "components/second-page"
  import  uploadConfirm from "components/upload-confirm"
  import  result from "components/result"
  import  loading from "base/loading"
  import  axios from 'common/js/http';
  import {mapGetters,mapMutations} from 'vuex'
    export default {
        name: "",
        components: {
          secondPage,
          loading,
          uploadConfirm,
          result
      },
      data(){
          return {
            pageOnOff:false,
            intoOnOff:true,
            confirmOnOff:false,
            imgSrc:'',
            options:{
              token:'',
              imgSrc:'',
              uploadTimer:'',
              createCanvasTimer:'',
            },
          }
      },
      computed:{
        ...mapGetters([
          'loadingOnOff',
        ])
      },
      created(){
        this.axiosInterceptors();
        this.isPlay()
      },
      methods:{
        upimgtime(){
          _czc.push(['_trackEvent', '上传图片', '时间', '', this.options.uploadTimer, 'js-btn-upload']);
        },
        upimgsize(size){
          _czc.push(['_trackEvent', '上传图片', '大小', '', size/1024, 'js-btn-upload']);
        },
        axiosInterceptors(){
          // http请求拦截器
          axios.interceptors.request.use(config => {
            this.setLoadingOnOff(true)
            return config
          }, error => {
            this.setLoadingOnOff(false)
            return Promise.reject(error)
          });
          axios.interceptors.response.use(
            response => {
              this.setLoadingOnOff(false)
              return response;
            },
            error => {
              this.setLoadingOnOff(false)
              return Promise.reject(error.response.data)
            });
        },
        firstPage(data){
            // console.log(data)
            this.imgSrc=data[0];
            this.pageOnOff=false;
            this.confirmOnOff=true;
            this.intoOnOff=false;
            this.options=data[1];
            this.options.token='';
        },
        showConfirm(){
          this.confirmOnOff=false;
        },
        test(){
          // let file = this.$refs.upload.files[0];
          let uploadInupt=this.$refs.upload
          let windowURL = window.URL || window.webkitURL;
          let dataURL;
            this.confirmOnOff=true;
            // this.imgSrc=this.$refs.upload.value
          if (uploadInupt && uploadInupt.files && uploadInupt.files[0]) {
            dataURL = windowURL.createObjectURL(uploadInupt.files[0]);
            this.imgSrc=dataURL;
            uploadInupt.value='';
          }
        },
        intoSecondPage(){
          sessionStorage.__INTOSECOND__=JSON.stringify(1);
          this.pageOnOff=true;
        },
        isPlay(){
          // console.log(sessionStorage.__INTOSECOND__)
          if(sessionStorage.__INTOSECOND__){
            this.intoOnOff=false
          }
          // this.setLoadingOnOff=false
        },
        uploadInit(){
          let file = this.$refs.upload.files[0];
          let fileType =['image/jpeg','image/jpg','image/png'];
          let filename = file.name;
          let type = ('image/'+filename.substring(filename.lastIndexOf('.')+1,filename.length)).toLocaleLowerCase();
          if ((fileType.join().toString()).indexOf(type) < 0) {
            alert('只允许上传JPG、PNG格式图片！');
            return false;
          }
          this.uploadCheck(file);
          this.$refs.upload.value = '';
        },
        uploadCheck(file){
          let token = this.options.token;
          let _this=this
          let startTimer = new Date().getTime();{
            if(token==''){
              this.getUploadToken(function(){
                _this.uploadImg(file)
              });
            }else{
              _this.uploadImg(file);
            }
          }
        },
        getUploadToken(cbk){
          let _this = this,callback = cbk || function(){};
          this.$jsonp(process.env.API_HOST+'public/index/qn_token').then((res)=>{
                // console.log(res)
                this.options.token = res;
                callback();
          })
          // axios.get(process.env.API_HOST+'public/index/qn_token',{withCredentials:false}).then((res)=>{
          //         // console.log(res)
          //         this.options.token = res.data;
          //         callback();
          // });
        },
        uploadImg(file){
          let Qiniu_UploadUrl = "http://up.qiniu.com",
              ops = this.options,
              token = ops.token;
          let startTimer = new Date().getTime();
          let formData = new FormData();
          let _this=this
          formData.append('token', token.upload_token);
          formData.append('key', token.key);
          formData.append('file', file);
          let xhr = new XMLHttpRequest();
          _this.setLoadingOnOff(true)
          xhr.open('POST', Qiniu_UploadUrl, true);
          xhr.onreadystatechange = function(response) {
            // console.log(xhr.readyState)
            // console.log(xhr.responseText)
            // console.log(xhr.status)
            if(xhr.readyState == 4 && xhr.status == 200 && xhr.responseText != "") {
              let imgSrc='http://mtapplet.meitudata.com/'+token.key ;
              _this.options.imgSrc = imgSrc;
               ops.uploadTimer = new Date().getTime() - startTimer;
              _this.setLoadingOnOff(false);
              _this.confirmOnOff=true;
              _this.imgSrc=imgSrc;
              _this.options.token='';
              _this.upimgtime();
              _this.upimgsize(file.size);
            }else if(xhr.status != 200 && xhr.responseText) {
              _this.setLoadingOnOff(false);
              alert('上传出错了，请返回重试~');
            }
            // this.options.token='';//一个token只能用一次
          };
          xhr.send(formData);
        },
        ...mapMutations({
          setLoadingOnOff:'SET_LOADING'
        })
      }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .wrap{

  }
.index-wrap{
  position: absolute;
  width: 100%;
  height: 17.8rem;
  background: url("../common/image/bg-home.jpg") no-repeat;
  background-size: 100% 100%;
  text-align: center;
}
  .home-gif{
    width: 10rem;
    margin-top: 4.5rem;
  }
  .Button{
    position: absolute;
    width: 5.2rem;
    height: 1.2rem;
    top: 14.48rem;
    left: 50%;
    margin-left: -2.6rem;
    background-image: url("../common/image/sprite.icons.png");
    background-position: -7.333333rem 0;
    background-size: 941px auto;
    -webkit-animation: animBtn 0.6s infinite;
    animation: animBtn 0.6s infinite;
    z-index: 10;
  }
  .Button input{
    display: block;
    width: 100%;
    height: 100%;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer;
  }

@-webkit-keyframes animBtn {
  0%, 49.9% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }

  50%, 100% {
    -webkit-transform: translateX(-0.469333rem);
    transform: translateX(-0.469333rem);
  }
}

@keyframes animBtn {
  0%, 49.9% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }

  50%, 100% {
    -webkit-transform: translateX(-0.469333rem);
    transform: translateX(-0.469333rem);
  }
}
</style>



